.about-us {
  @extend %page-main-base;
}

.about-info {
  @include container-fluid-base;
}

.about-info-row {
  @extend %row;
}

.about-info-col {
  @extend %col-sm-11;
  @extend %col-lg-8;
  @extend %mx-auto;
  @extend %text-left;
  @extend %text;
}

.employees {
  @extend %wide-column;
}

.employees-row {
  @extend %row;
}

.employee-img {
  filter: grayscale(1);
}

@include semantic-card('employee', $clickable: false, $title-alignment: center,
  $title-color: $body-color, $subtitle-color: $text-muted, $footer-border-off: false,
  $card-img-height: 275px);

@include multi-col-spacing('employee-col', $mb-size: 4, $mb-md-size: 5);

.employee-footer {
  font-size: $font-size-large;

  a {
    @include link-decoration($gray-light, $brand-primary, none, none);
  }

  @include media-breakpoint-up(sm) {
    font-size: $font-size-x-large;
  }
}
