.post {
  @include container-fluid-base;
  @extend %mt-5;
  @extend %mt-md-8;
}

.post-row {
  @extend %row;
}

.post-col {
  @extend %col-sm-11;
  @extend %col-lg-8;
  @extend %mx-auto;
}

%post-header-footer-font-base {
  color: $post-header-footer-color;
  font-size: $font-size-small;
}

.post-header {
  @extend %mb-5;
}

.post-title {
  @extend %mb-3;
}

.post-byline {
  @extend %mb-0;
  @extend %post-header-footer-font-base;
}

.post-author {
  a {
    @include link-decoration($post-header-footer-link-color, $post-header-footer-link-hover-color,
      $post-header-footer-link-border, $post-header-footer-link-border-hover);
  }
}

.post-author-comma {
  margin-left: -4px;
}

.post-date {
  @extend %d-block;
}

.post-feature-img {
  @extend %img-fluid;
  @extend %d-block;
  @extend %mt-4;
}

.post-content {
  @extend %narrative-base;
  @extend %mb-5;
}

.post-table {
  @extend %table;
  @extend %table-bordered;
  @extend %my-4;

  @include media-breakpoint-down(sm) {
    display: block;
    width: 100%;
    border: 0;
    overflow-x: auto;
    -ms-overflow-style: -ms-autohiding-scrollbar;
  }
}

.post-blockquote {
  @extend %blockquote;
  @extend %my-4;

  cite::before {
    content: ' ';
  }

  &.excerpt {
    font-size: $font-size-x-small;
    background-color: lighten($gray, 63%);
  }
}

.post-footer {
  @extend %pt-4;
  border-top: 1px solid $gray-lightest;
}

.post-contributors {
  @extend %mb-4;
  @extend %post-header-footer-font-base;
}

.post-tags {
  @extend %mb-4;

  a {
    @extend %badge;
    background-color: $brand-primary;
  }

  a.tag-badge {
    margin-right: 5px;

    &:hover,
    &:focus {
      background-color: $link-hover-color;
    }
  }
}

.post-share {
  div {
    @extend %d-sm-inline-block;

    &:first-child {
      @extend %mb-3;
      @extend %mb-sm-0;
      @extend %mr-sm-2;
    }
  }

  a {
    @extend %btn-base;
    @extend %btn-icon-left;
    @extend %btn-sm;
    @include button-outline-variant-2($btn-share-color, $btn-share-bg,
      $btn-share-border, $btn-share-active-color);
  }
}

.post-related {
  @extend %mt-5;

  h3,
  ul {
    @extend %post-header-footer-font-base;
  }

  h3 {
    @extend %text-uppercase;
    @extend %mb-3;
  }

  ul {
    @extend %list-unstyled;
    @extend %mb-0;

    li:not(:last-child) {
      @extend %pb-3;
    }
  }

  a {
    @include link-decoration($post-header-footer-link-color, $post-header-footer-link-hover-color,
      $post-header-footer-link-border, $post-header-footer-link-border-hover);
  }
}
