.page--join__text {
  h2 {
    font-size: $font-size-xxx-large;
    margin-top: 5rem;
  }

  h3 {
    font-size: $font-size-small;
    letter-spacing: 0.05em;
    line-height: 1.375;
    margin-top: 1.5em;
    text-transform: uppercase;
  }

  h2,
  h3 {
    + p {
      margin-top: 0.5em;
    }
  }

  p + p {
    margin-top: 0.5em;
  }

  p {
    margin-bottom: 0;
  }

  .quote-text + p {
    font-size: $font-size-medium;
  }
}

.section__care-package {
  h3 {
    margin-top: 1em;
  }
}

.open-positions-list {
  ul {
    list-style-type: none;
    margin-bottom: 2.5em;
    padding-left: 0;
    @extend %mt-4;

    li:not(:last-child) {
      @extend %mb-4;
    }

    p {
      font-size: $font-size-x-small;
    }
  }

  a {
    font-weight: bold;
    font-size: $font-size-large;
  }

  li {
    border: 2px solid $gray-lightest;
  }
}

.open-positions-heading.open-positions-heading {
  margin-top: 2.5em;
}

.care-square {
  background-color: $sk-blue;
  width: 60px;
  height: 60px;
}

.subnav-col {
  li {
    .active:after,
    &:hover {
      border-left-color: $sk-yellow;
    }
  }
}

.employee-gradient__img::after {
  height: 100%;
  margin-left: 15px;
  width: calc(100% - 30px);
}

.image-list__employee-img {
  filter: grayscale(1);
}
